:root {
  /*primary*/
  --clr-primary-100: hsl(225, 100%, 94%);
  --clr-primary-200: hsl(245, 75%, 52%);
  /*neutral*/
  --clr-neutral-100: hsl(225, 100%, 98%);
  --clr-neutral-200: hsl(224, 23%, 55%);
  --clr-neutral-300: hsl(223, 47%, 23%);
}

:root {
  --ff-primary: "Red Hat Display" , sans-serif;
}

:root {
  --fs-primary: 1rem;
}

:root {
  --fw-primary-500: 500;
  --fw-primary-700: 700;
  --fw-primary-900: 900;
}

