/****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:active {
  cursor: pointer;
  color: var(--clr-primary-200);
  font-weight: var(--fw-primary-700);
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--ff-primary);
  font-size: var(--fs-primary);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--clr-primary-100);
  padding: 0 1em;
}
body::before {
  content: "";
  position: absolute;
  background: url(../../../images/pattern-background-desktop.svg);
  background-size: cover;
  z-index: -1;
  height: 90vh;
  top: -45vh;
  left: 0;
  right: 0;
}

.main {
  max-width: 500px;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: lightcyan;
  text-align: center;
  border-radius: 15px;
  overflow: hidden;
  background-color: #ffff;
  box-shadow: 2px 10px 20px lightgray;
}
.main__image {
  object-fit: cover;
}
.main__wrapper {
  padding: 2em;
}
.main__wrapper > * {
  margin-bottom: 1.3em;
}
.main__title {
  font-weight: var(--fw-primary-900);
  font-size: 2rem;
  color: var(--clr-neutral-300);
}
.main__description {
  line-height: 1.5;
  letter-spacing: 1px;
  max-width: 35ch;
  color: var(--clr-neutral-200);
}
.main__annual-plan {
  display: flex;
  justify-content: space-between;
  padding: 1em 2em;
  align-items: center;
  background-color: var(--clr-neutral-100);
  border-radius: 10px;
}
.main__annual-plan__img {
  flex: 0 1;
}
.main__annual-plan__container {
  margin-left: 1em;
}
.main__annual-plan__title {
  font-weight: var(--fw-primary-900);
  margin-bottom: 0.5em;
  color: var(--clr-neutral-300);
}
.main__annual-plan__price {
  color: var(--clr-neutral-200);
}
.main__annual-plan__container--bg {
  display: flex;
  align-items: center;
  flex-basis: 70%;
}
.main__buttons {
  font-weight: var(--fw-primary-700);
}
.main__buttons__confirm {
  padding: 1em 2em;
  background-color: var(--clr-primary-200);
  color: white;
  border-radius: 10px;
  margin-bottom: 1em;
  box-shadow: 2px 5px 20px lightgray;
  cursor: pointer;
}
.main__buttons__cancel {
  color: var(--clr-neutral-200);
  cursor: pointer;
}